.btn {
  display: inline-flex;
  vertical-align: middle;
  padding: var(--s-2) var(--s4);
  font-weight: bold;
  border-radius: var(--radius-rounded);
  text-decoration: none;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  text-align: center;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: var(--border-line-thick) solid transparent;
  position: relative;
}
.btn:active {
  transform: scale(0.96);
}

.btn-primary:link {
  color: var(--color-white);
  background: var(--color-primary);
  border-color: var(--color-primary);
}
.btn-primary:visited {
  color: var(--color-white);
  background: var(--color-primary);
  border-color: var(--color-primary);
}
.btn-primary:hover, .btn-primary:focus-within {
  color: var(--color-primary);
  background: var(--color-white);
  border-color: var(--color-primary);
}

.btn-primary-outline:link {
  color: var(--color-primary);
  background: transparent;
  border-color: var(--color-primary);
}
.btn-primary-outline:visited {
  color: var(--color-primary);
  background: transparent;
  border-color: var(--color-primary);
}
.btn-primary-outline:hover, .btn-primary-outline:focus-within {
  color: var(--color-white);
  background: var(--color-primary);
  border-color: var(--color-primary);
}

.btn-secondary:link {
  color: var(--color-white);
  background: var(--color-secondary);
  border-color: var(--color-secondary);
}
.btn-secondary:visited {
  color: var(--color-white);
  background: var(--color-secondary);
  border-color: var(--color-secondary);
}
.btn-secondary:hover, .btn-secondary:focus-within {
  color: var(--color-secondary);
  background: var(--color-white);
  border-color: var(--color-secondary);
}

.btn-secondary-outline:link {
  color: var(--color-secondary);
  background: transparent;
  border-color: var(--color-secondary);
}
.btn-secondary-outline:visited {
  color: var(--color-secondary);
  background: transparent;
  border-color: var(--color-secondary);
}
.btn-secondary-outline:hover, .btn-secondary-outline:focus-within {
  color: var(--color-white);
  background: var(--color-secondary);
  border-color: var(--color-secondary);
}

.card {
  border: var(--border-line) solid var(--color-primary-xlite);
  display: flex;
  flex-direction: column;
  background-color: var(--color-white);
  position: relative;
  height: 100%;
  justify-content: space-between;
}

.card-body {
  padding: var(--gutter);
}

.card-content {
  display: flex;
  align-items: center;
}

.card-thumb {
  flex: 1 0 8.75rem;
  margin-right: var(--gutter);
}

.card-img {
  width: 100%;
  height: auto;
}

.card .btn {
  align-self: center;
  min-width: 30ch;
  max-width: 100%;
  margin-bottom: 0;
}

.flickity-enabled {
  position: relative;
}

.flickity-enabled:focus {
  outline: 0;
}

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.flickity-button {
  position: absolute;
  background: rgba(255, 255, 255, 0.75);
  border: none;
  color: #333;
}

.flickity-button:hover {
  background: #fff;
  cursor: pointer;
}

.flickity-button:focus {
  outline: 0;
  box-shadow: 0 0 0 5px #19f;
}

.flickity-button:active {
  opacity: 0.6;
}

.flickity-button:disabled {
  opacity: 0.3;
  cursor: auto;
  pointer-events: none;
}

.flickity-button-icon {
  fill: currentColor;
}

.flickity-prev-next-button {
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  transform: translateY(-50%);
}

.flickity-prev-next-button.previous {
  left: 10px;
}

.flickity-prev-next-button.next {
  right: 10px;
}

.flickity-rtl .flickity-prev-next-button.previous {
  left: auto;
  right: 10px;
}

.flickity-rtl .flickity-prev-next-button.next {
  right: auto;
  left: 10px;
}

.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

.flickity-page-dots {
  position: absolute;
  width: 100%;
  bottom: calc(-1 * var(--s2));
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}

.flickity-rtl .flickity-page-dots {
  direction: rtl;
}

.flickity-page-dots .dot {
  display: inline-block;
  width: var(--s-1);
  height: var(--s-1);
  margin: 0 var(--s-4);
  background: var(--color-primary);
  border-radius: 50%;
  cursor: pointer;
}

.flickity-page-dots .dot.is-selected {
  background: var(--color-secondary);
}

/* flickity-fade */
.flickity-enabled.is-fade .flickity-slider > * {
  pointer-events: none;
  z-index: 0;
}

.flickity-enabled.is-fade .flickity-slider > .is-selected {
  pointer-events: auto;
  z-index: 1;
}

.carousel {
  position: relative;
  top: var(--s-5);
}

.carousel-cell {
  width: 100%;
}

.carousel-cell-image {
  display: block;
  max-height: 100%;
  max-width: 100%;
  opacity: 0;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}

/* fade in lazy loaded image */
.carousel-cell-image.flickity-lazyloaded,
.carousel-cell-image.flickity-lazyerror {
  opacity: 1;
}

.section-slider {
  position: relative;
}

.section-slider::before {
  content: "";
  width: 100%;
  height: 70%;
  display: block;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-primary-lite);
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));
}

.carousel img {
  width: 100%;
  height: auto;
}

.site-header {
  position: relative;
  background-color: var(--color-secondary);
}
.site-header > * {
  color: var(--color-white);
}

.site-nav a {
  font-weight: bold;
  color: var(--color-lite);
}
.site-nav a:hover, .site-nav a:focus {
  color: var(--color-white);
}
.site-nav a:active {
  color: var(--color-white);
}

:root {
  --grid-header-col: 1fr 1fr 2fr minmax(max-content, 1fr) 10%;
  --grid-header-row: auto 1rem;
}

@media (min-width: 62rem) {
  .header-container {
    display: grid;
    text-align: center;
    grid-template-columns: var(--grid-header-col);
    position: relative;
  }
}

@media (min-width: 62rem) {
  .site-brand {
    display: grid;
    grid-column: 1/-1;
    grid-row: 1/-1;
    place-items: center;
  }
}

@media (min-width: 62rem) {
  .site-logo {
    padding: var(--s0) var(--s1);
    display: inline-block;
  }
}

.site-logo > svg {
  fill: var(--color-white);
}

@media (min-width: 62rem) {
  .site-logo {
    box-shadow: 0 0 0 var(--s-2) var(--color-secondary), 0 var(--s-4) var(--s-2) var(--s-4) rgba(0, 0, 0, 0.3);
    position: relative;
    top: var(--s-2);
    z-index: 2;
  }
}

@media (min-width: 62rem) {
  .site-logo::before {
    content: "";
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--color-secondary);
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 62rem) {
  .site-nav {
    grid-column: 1/-1;
    grid-row: 1;
  }
}

@media (min-width: 62rem) {
  .site-nav-list {
    height: 100%;
    display: grid;
    grid-template-columns: var(--grid-header-col);
  }
}

@media (min-width: 62rem) {
  .site-nav-list li:last-child {
    grid-column: 4/5;
  }
}

.site-nav-item {
  text-align: left;
}
@media (min-width: 62rem) {
  .site-nav-item {
    height: 100%;
  }
}

@media (min-width: 62rem) {
  .site-nav-link {
    padding: var(--s1);
    display: block;
  }
}

@media (min-width: 62rem) {
  .site-nav-item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media (min-width: 62rem) {
  .site-nav-list-child {
    display: none;
    border-radius: var(--radius-rounded);
    box-shadow: 0 var(--s-6) var(--s-6) 0 rgba(0, 0, 0, 0.2);
    z-index: 10;
    position: absolute;
    top: calc(100% - 1 * var(--s0));
    left: 0;
    background-color: var(--color-secondary-lite);
  }
}

.sr-only, .skip-link {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
  top: 0;
}

.skip-link:focus {
  border-radius: 0;
  position: absolute;
  clip: auto !important;
  -webkit-clip-path: none;
  clip-path: none;
  display: block;
  width: 100%;
  height: auto;
  left: 0;
  top: 0;
  z-index: 102;
}

